
.Burgermenu {
    position: fixed;
    top: 0;
    left: 0;
    width: 250px;
    height: 100%;
    background-color: #ffffff;
    @media screen and (min-width: 768px) {
        position: fixed;
            top: 0;
            left: 0;
            width: 400px;
            height: 100%;
            background-color: #ffffff;
    }
       
}

.MenuPage{
position: absolute;
width: 100%;
height: 100%;
top: 0;
background-color: #b78d8d;
}

.menuTitle {
    color: #000000;
    /* White color */
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 30px;
    padding: 20px 20px 19px 20px;
}

.Burgermenu {
    flex-direction: column;
    align-items: center;
    background-color: #ffffff;
    border: 1px solid #dddddd;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
}


.burgerClose {
    background: #ffffff;
    border: 1px solid;
    border-color: #d1d1d1;
    border-radius: 100%;
    padding: 1px;
    height: 50px;
    width: 50px;
    margin-left: 220px;
    top: 0;
    position: fixed;
    @media screen and (min-width: 768px) {
    
            background: #ffffff;
                border: 1px solid;
                border-color: #d1d1d1;
                border-radius: 100%;
                padding: 1px;
                height: 50px;
                width: 50px;
                margin-left: 370px;
                top: 0;
                position: fixed;
        }
}

.burgerClose:hover {
    background-color: #f4f4f4;
  
}

